import React, { Component } from 'react'

export class ServiceLayout extends Component {
    render() {
        return (
            
                <div className="col-lg-3 col-md-6 text-center">
                     <div className="mt-5">
                        <i className={`fas fa-4x ${this.props.icon} text-primary mb-4`}></i>
                        <h3 className="h4 mb-2">{this.props.title}</h3>
                        <p className="text-muted mb-0">{this.props.Description}</p>
                     </div>
                </div>
            
        )
    }
}

export default ServiceLayout
