import React, { Component } from 'react'
import SinglePortfolio from './SingleLayout'

const portfolio = [
    {image:"assets/img/portfolio/fullsize/1.jpg" ,icon:"assets/img/portfolio/thumbnails/1.jpg"},
    {image:"assets/img/portfolio/fullsize/2.jpg" ,icon:"assets/img/portfolio/thumbnails/2.jpg"},
    {image:"assets/img/portfolio/fullsize/3.jpg" ,icon:"assets/img/portfolio/thumbnails/3.jpg"},
    {image:"assets/img/portfolio/fullsize/4.jpg" ,icon:"assets/img/portfolio/thumbnails/4.jpg"},
    {image:"assets/img/portfolio/fullsize/5.jpg" ,icon:"assets/img/portfolio/thumbnails/5.jpg"},
    {image:"assets/img/portfolio/fullsize/6.jpg" ,icon:"assets/img/portfolio/thumbnails/6.jpg"}

]

export class Portfolio extends Component {
    render() {
        return (
            <div>
                <div id="portfolio">
                    <div className="container-fluid p-0">
                        <div className="row no-gutters">
                            { portfolio.map((portfolio,index)=>
                            {
                                return <SinglePortfolio {...portfolio} key={index} /> 
                            })}
                        </div>
                    </div>
                </div>
                <section className="page-section bg-dark text-white">
                    <div className="container text-center">
                        <h2 className="mb-4">Free Download at Start Bootstrap!</h2>
                        <a className="btn btn-light btn-xl" href="https://startbootstrap.com/themes/creative/">Download Now!</a>
                    </div>
                </section>
            </div>
            
        )
    }
}

export default Portfolio
