import React, { Component } from 'react'
import ServiceLayout from '../layout/ServiceLayout'
const service = [
    {title:'Sturdy Themes', Description:'Our themes are updated regularly to keep them bug free!',icon:'fa-gem'},
    {title:'Up to Date', Description:'All dependencies are kept current to keep things fresh',icon:'fa-laptop'},
    {title:'Ready to Publish', Description:'You can use this design as is, or you can make changes!',icon:'fa-globe'},
    {title:'Made with Love', Description:'Is it really open source if its not made with love?',icon:'fa-heart'}
]; 

export class Service extends Component {
    render() {
        return (
                <section className="page-section" id="services">
                    <div className="container">
                        <h2 className="text-center mt-0">At Your Service</h2>
                        <hr className="divider my-4" />
                        <div className="row">
                           {service.map((service,index)=> {
                               return <ServiceLayout {...service} key={index} />
                            })} 
                        </div>
                    </div>
                </section>
        )
    }
}

export default Service
